body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #121212;
  background-color: #ffffff;
  font-size: 16px;
}

h1 {
  font-size: 42px;
  margin-top: 0;
  margin-bottom: 24px;
}

p {
  line-height: 24px;
}

img {
  width: 100%;
}

section {
  padding: 96px 0;
}

.btn {
  color: #ffffff;
  background-color: #e4c1ae;
  border: none;
  border-radius: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 46px;
  width: 128px;
  margin-top: 16px;
  transition: 0.3s;
  cursor: pointer;
}
.btn:hover {
  background-color: rgb(164, 138, 124);
}
.btn:focus, .btn:active {
  background-color: rgb(164, 138, 124);
  -webkit-box-shadow: inset 0px 5px 5px rgba(23, 23, 23, 0.3);
  box-shadow: inset 0px 5px 5px rgba(23, 23, 23, 0.3);
}
.btn:focus-visible {
  outline: none;
}

.btn-wider {
  width: 180px;
}

.btn-cross {
  position: absolute;
  right: 46px;
  top: 46px;
  width: 12px;
  height: 12px;
  cursor: pointer;
}
.btn-cross:before, .btn-cross:after {
  position: absolute;
  left: 5px;
  content: " ";
  height: 15px;
  width: 2px;
  background-color: #000;
  border-radius: 2px;
}
.btn-cross:before {
  transform: rotate(45deg);
}
.btn-cross:after {
  transform: rotate(-45deg);
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 30px;
}

.box {
  max-width: 980px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.box-img {
  max-width: 505px;
  width: 100%;
}
.box-desc {
  max-width: 380px;
  width: 100%;
  padding-left: 20px;
}

#overlay {
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(18, 18, 18, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 0;
}
#overlay.show {
  display: block;
}

#popup {
  display: none;
}

.popup-content {
  background-color: #ffffff;
  max-width: 730px;
  margin: 0 auto;
  padding: 56px 95px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  z-index: 3;
}

@media (max-width: 1024px) {
  h1 {
    font-size: 36px;
    margin-bottom: 18px;
  }
  section {
    padding: 48px 0;
  }
  .popup-content {
    padding: 30px 55px;
  }
  .btn-cross {
    height: 18px;
    right: 30px;
    top: 30px;
    width: 18px;
  }
  .btn-cross:after, .btn-cross:before {
    height: 18px;
    left: 7px;
  }
}
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
  h1 {
    font-size: 24px;
    margin-bottom: 18px;
  }
  p {
    line-height: 20px;
    margin: 10px 0;
  }
  .btn {
    line-height: 40px;
    margin-top: 10px;
    max-width: 119px;
    font-size: 14px;
  }
  .btn-wider {
    width: 140px;
  }
  .popup-content {
    padding: 45px;
  }
}
@media (max-width: 600px) {
  .box {
    flex-wrap: wrap;
  }
  .box-img, .box-desc {
    max-width: 100%;
  }
  .box-desc {
    padding: 0;
    margin-top: 20px;
  }
}
@media (max-width: 480px) {
  section {
    padding: 24px 0;
  }
  .container {
    padding: 0 10px;
  }
  .btn-cross {
    right: 20px;
    top: 20px;
  }
  .popup-content {
    padding: 20px;
  }
}